.coberturas {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}

.cobertura {
    align-items: center;
    display: flex;
    flex-flow: column wrap;
    margin-bottom: 16px;
    width: 50%;
    @media screen and (min-width: 1024px) {
        width: 16.66%;
    }
    

    &-picture {

    }

    &-legend {
        font-size: 0.75rem;
        font-weight: bold;
        margin-top: 8px;
        text-align: center;
        text-transform: uppercase;
    }
}
